.order {
  &-info {
    margin-bottom: 68px;

    @media(max-width: 1440px) {
      margin-bottom: 50px;
    }

    @media(max-width: 1280px) {
      margin-bottom: 40px;
    }

    &.margin-42 {
      margin-bottom: 42px;

      @media(max-width: 1440px) {
        margin-bottom: 24px;
      }

      @media(max-width: 1280px) {
        margin-bottom: 14px;
      }
    }

    // .ui-radio+label {
    //   padding-top: 0;
    //   padding-bottom: 0;
    // }

    .ui-btn {
      max-width: 100%;
    }

    @media(min-width: 1024px) {
      .col-order-3 {
        flex: 0 0 33.3%;
        max-width: 33.3%;
        padding-left: 13px;
        padding-right: 13px;
      }
    }

    .col-md-6 {
      padding-left: 13px;
      padding-right: 13px;
    }

    &-title {
      font-family: 'FuturaMediumC';
      font-size: 36px;
      margin-bottom: 38px;

      @media(max-width: 1440px) {
        font-size: 32px;
        margin-bottom: 30px;
      }

      @media(max-width: 1280px) {
        font-size: 30px;
        margin-bottom: 15px;
      }

      @media(max-width: 1024px) {
        font-size: 28px;
        margin-bottom: 20px;
      }

      @media(max-width: 576px) {
        font-size: 24px;
      }

      &.margin-30 {
        margin-bottom: 30px;

        @media(max-width: 1440px) {
          margin-bottom: 20px;
        }

        @media(max-width: 1280px) {
          margin-bottom: 15px;
        }

        // @media(max-width: 1024px) {
        //   margin-bottom: 20px;
        // }
      }
    }

    &-subtitle {
      font-family: 'FuturaBookC';
      font-size: 20px;
      margin-bottom: 40px;

      @media(max-width: 1440px) {
        margin-bottom: 30px;
      }

      @media(max-width: 1280px) {
        margin-bottom: 25px;
      }

      @media(max-width: 576px) {
        font-size: 18px;
      }

      a {
        border-bottom: 1px solid #172542;

        &:hover {
          border-color: #a8bed6;
        }
      }
    }

    .ui-field {
      margin-bottom: 26px;
      position: relative;

      @media(max-width: 576px) {
        margin-bottom: 15px;
      }

      input {
        &.error {
          &:focus {
            &+.ui-label {
              display: block;
              position: absolute;
              left: 20px;
              top: -9px;
              color: #bc0000;
              font-family: 'FuturaMediumC';
              background: #fff;
              padding: 0 5px;
            }

            &::placeholder {
              color: transparent;
            }
          }
        }

        &:focus {
          &+.current {
            display: block;
            position: absolute;
            left: 20px;
            top: -9px;
            color: #728EAE;
            font-family: 'FuturaMediumC';
            background: #fff;
            padding: 0 5px;
          }

          &::placeholder {
            color: transparent;
          }
        }
      }

      &.margin-0 {
        margin-bottom: 0;
      }

      &.margin-30 {
        margin-bottom: 30px;
      }
    }

    .ui-input {
      color: #172542;
      font-size: 20px;

      &::placeholder {
        color: #728EAE;
      }
    }
  }

  &-form {
    .cart-item {
      padding: 9px 20px 9px 9px;

      @media(max-width: 1310px) {
        flex-direction: column;
        align-items: flex-start;
      }

      @media(max-width: 1024px) {
        flex-direction: row;
        align-items: center;
      }

      @media(max-width: 525px) {
        flex-direction: column;
        align-items: flex-start;
      }

      @media(max-width: 340px) {
        padding: 10px;
      }
    }

    .cart-item-txt {
      @media(max-width: 1024px) {
        margin-bottom: 0;
      }
    }

    .cart-item-img {
      max-width: 76px;
      margin-right: 8px;
    }

    .cart-item-title {
      font-size: 18px;
      max-width: 221px;
      font-family: 'FuturaBookC';
      margin-bottom: 0;

      // @media(max-width: 1024px) {
      //   margin-bottom: 10px;
      // }

      @media(max-width: 525px) {
        max-width: 100%;
      }
    }

    .cart-item-sum {
      font-family: 'FuturaBookC';
      font-size: 18px;

      &.display {
        @media(max-width: 480px) {
          display: block;
        }
      }

      @media(max-width: 525px) {
        font-size: 16px;
      }
    }
  }

  &-option {
    margin-bottom: 13px;

    .error {
      display: block;
      margin-top: 5px;

      @media(max-width: 1170px) {
        margin-top: -7px;
      }

      @media(max-width: 1024px) {
        margin-top: 5px;
      }

      @media(max-width: 576px) {
        margin-top: -7px;
      }
    }

    // .ui-select {
    //   @media(max-width: 1170px) {
    //     height: calc(100% - 12px);
    //     margin-bottom: 12px;
    //   }

    //   // @media(max-width: 1024px) {
    //   //   height: 100%;
    //   //   margin-bottom: 0;
    //   // }

    //   @media(max-width: 576px) {
    //     height: calc(100% - 12px);
    //     margin-bottom: 12px;
    //   }
    // }

    &.margin-0 {
      margin-bottom: -12px;
    }
  }

  &-warn {
    position: relative;

    &.mb-40 {
      margin-bottom: 40px;
    }

    &-img {
      position: absolute;
      left: 0;
      top: -8px;

      @media(max-width: 1440px) {
        top: 0;
        width: 40px;
        height: 40px;
      }
    }

    &-txt {
      padding-left: 64px;
      font-size: 18px;
      color: #728EAE;

      @media(max-width: 1440px) {
        font-size: 16px;
        padding-left: 54px;
      }

      span {
        font-family: 'FuturaMediumC';

        &.black {
          display: block;
          color: #172542;
          margin-bottom: 24px;

          @media(max-width: 1440px) {
            margin-bottom: 20px;
          }

          @media(max-width: 1280px) {
            margin-bottom: 15px;
          }
        }
      }
    }
  }
}

@media(min-width: 1024.99px) {
  .col-order-5 {
    flex: 0 0 37.66667%;
    max-width: 37.66667%;
  }

  .col-order-7 {
    flex: 0 0 62.33333%;
    max-width: 62.33333%;
  }

  .col-order-8 {
    flex: 0 0 66.5%;
    max-width: 66.5%;
    padding-left: 13px;
    padding-right: 13px;
  }
}


.order-form {
  .cart-item-title {
    max-width: none;
  }

  @media (max-width: 1024px) and (min-width: 576px) {
    .cart-item-info.txt {
      padding-right: 20px;
    }
  }
}