.result {
  text-align: center;
  max-width: 730px;
  margin: 0 auto;
  margin-bottom: 117px;

  @media(max-width: 1024px) {
    margin-bottom: 80px;
  }

  &-lines {
    &.flex {
      align-items: center;
      justify-content: center;
    }
  }

  &-img-txt {
    margin-bottom: 30px;
  }

  &-img {
    margin-bottom: 62px;

    @media(max-width: 1440px) {
      margin-bottom: 25px;
    }

    @media(max-width: 1024px) {
      margin: 0 50px;
    }
  }

  &-txt {
    font-size: 32px;
    letter-spacing: 0.05em;
    color: #172542;
    margin-bottom: 20px;

    @media(max-width: 768px) {
      font-size: 28px;
    }

    @media(max-width: 576px) {
      font-size: 20px;
    }
  }

  &-line {
    width: 52px;
    height: 1px;
    background: #172542;
  }

  &-circle {
    width: 17px;
    height: 17px;
    border-radius: 50%;
    margin: 0 10px;
    background: #FAD89D;
  }

  img {
    margin-bottom: 30px;
  }

  &-search {
    margin: 0 15px;

    @media(max-width: 576px) {
      margin: 0;
    }

    &.flex {
      @media(max-width: 576px) {
        flex-direction: column;
      }
    }

    .header-control-search {
      margin-right: 16px;
      max-width: 100%;
    }

    .ui-btn {
      max-width: 152px;
      width: 100%;

      @media(max-width: 576px) {
        max-width: 100%;
      }
    }

    .ui-input {
      border-radius: 32px;
      padding-left: 50px;

      @media(max-width: 576px) {
        margin-bottom: 10px;
      }
    }
  }

  .header-control-search {
    img {
      position: absolute;
      left: 21px;
      top: 16px;
      margin-bottom: 0;
    }
  }
}

.services {
  margin-bottom: 96px;

  @media(max-width: 1440px) {
    margin-bottom: 40px;
  }

  @media(max-width: 1280px) {
    margin-bottom: 30px;
  }

  &-block {
    @media(max-width: 1024px) {
      margin-bottom: 50px;
    }
  }

  &.margin-58 {
    margin-bottom: 60px;
  }

  &-card {
    max-width: 370px;
    text-align: center;
    position: relative;
    min-height: calc(100% - 40px);
    margin: 0 auto 40px auto;

    @media(max-width: 1024px) {
      max-width: 100%;
      min-height: calc(100% - 30px);
      margin: 0 auto 30px auto;
    }

    @media(max-width: 576px) {
      min-height: calc(100% - 20px);
      margin: 0 auto 20px auto;
    }

    &.light-blue {
      background: #DBEEF5;
    }

    &.light-orange {
      background: #F7E5D0;
    }

    &.blue {
      background: #BFD4ED;
    }

    &.light-yellow {
      background: #F7F0E7;
    }

    img {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);

      @media(max-width: 1366px) {
        max-height: 90px;
      }
    }
  }

  &-txt {
    padding: 46px 50px 152px 50px;

    @media(max-width: 1366px) {
      padding: 46px 10px 102px;
    }

    @media(max-width: 576px) {
      padding: 20px;
    }
  }

  &-title {
    font-size: 24px;
    color: #172542;
    font-family: 'FuturaMediumC';
    margin-bottom: 12px;
    letter-spacing: 0.05em;

    @media(max-width: 1366px) {
      max-width: 270px;
      margin: 0 auto 12px;
    }
  }

  &-subtitle {
    line-height: 1.2;
    color: #172542;
    letter-spacing: 0.05em;

    @media(max-width: 1366px) {
      max-width: 270px;
      margin: 0 auto;
    }
  }

  &-img {
    @media(max-width: 576px) {
      display: none;
    }
  }
}

.commercial {
  // margin-bottom: 131px;

  @media(max-width: 1024px) {
    display: none;
  }

  &-img {
    margin-right: 40px;
    padding-top: 105px;
    max-width: 370px;

    &:last-child {
      margin-right: 0;
    }

    &-txt {
      text-transform: uppercase;

      img {
        margin-bottom: 35px;
      }
    }
  }

  &-title {
    font-size: 30px;
    margin-bottom: 20px;

    strong {
      font-size: 45px;
      letter-spacing: .05em;
    }
  }

  &-subtitle {
    font-size: 30px;

    @media(max-width: 1090px) {
      font-size: 20px;
    }
  }
}

.slider {
  &-items {
    margin-bottom: 84px;

    .swiper-wrapper {
      margin-bottom: 80px;

      @media(max-width: 576px) {
        margin-bottom: 60px;
      }
    }

    .swiper-slide {
      height: auto !important;
    }

    &.margin-17 {
      margin-bottom: 17px;
    }

    .swiper {
      display: none;

      &.active {
        display: flex;
      }
    }

    .btn-new-prev, .btn-new-next {
      position: static;
      width: 100%;
      height: 100%;
      margin-top: 0;
    }

    .btn-new-prev {
      margin-right: 24px;
    }

    .btn-new-prev:after {
      content: '';
    }

    .btn-new-next:after {
      content: '';
    }

    .btn-rec-prev, .btn-rec-next {
      position: static;
      width: 100%;
      height: 100%;
      margin-top: 0;
    }

    .btn-rec-prev {
      margin-right: 24px;
    }

    .btn-rec-prev:after {
      content: '';
    }

    .btn-rec-next:after {
      content: '';
    }
  }

  &-tab {
    display: none;

    .slider-item {
      padding-bottom: 95px;
      margin-left: auto;
      margin-right: auto;

      @media(max-width: 576px) {
        padding-bottom: 55px;
      }
    }

    &.active {
      display: block;
    }
  }

  &-item {
    max-width: 370px;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;

    &.pb-95 {
      padding-bottom: 95px;

      @media(max-width: 1440px) {
        padding-bottom: 70px;
      }

      @media(max-width: 576px) {
        padding-bottom: 55px;
      }
    }

    @media(max-width: 480px) {
      margin: 0 auto;
      display: block;
      max-width: 100%;
    }

    &-block {
      margin-bottom: 30px;
      position: relative;

      @media(max-width: 1440px) {
        margin-bottom: 20px;
      }

      @media(max-width: 480px) {
        max-width: 100%;
        margin: 0 auto 30px;
      }
    }

    &-img {
      // margin-bottom: 30px;
      object-fit: contain;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    &-status {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
    }

    &-link {
      position: relative;
      color: #172542;

      &.img {
        overflow: hidden;
        position: relative;
        display: block;
        height: 0;
        padding-bottom: 100%;
      }
    }

    &-title {
      font-family: 'FuturaDemiC';
      font-size: 24px;
      color: #172542;
      margin-bottom: 12px;
      font-weight: 600;
      line-height: 1.2;
      letter-spacing: 0.05em;
      min-height: 56px;

      @media(max-width: 1440px) {
        font-size: 22px;
        min-height: 52px;
      }

      @media(max-width: 1280px) {
        font-size: 20px;
        min-height: 48px;
      }

      @media(max-width: 768px) {
        font-size: 17px;
        margin-bottom: 10px;
      }

      @media(max-width: 480px) {
        min-height: auto;
        margin-bottom: 10px;
      }
    }

    &-subtitle {
      font-size: 20px;
      color: #9BADC2;
      margin-bottom: 16px;
      font-weight: 400;
      line-height: 1.2;
      letter-spacing: 0.05em;

      @media(max-width: 1440px) {
        font-size: 18px;
        margin-bottom: 10px;
      }

      // @media(max-width: 1024px) {
      //   font-size: 18px;
      // }

      @media(max-width: 768px) {
        font-size: 16px;
        // margin-bottom: 10px;
      }
    }
  }

  &-price {
    margin-bottom: 17px;

    @media(max-width: 1440px) {
      margin-bottom: 10px;
    }

    @media(max-width: 768px) {
      margin-bottom: 7px;
    }

    &.flex {
      justify-content: space-between;
      align-items: center
    }

    &-num {
      &.flex {
        flex-wrap: wrap;
      }
    }

    &-present {
      font-family: 'FuturaDemiC';
      font-weight: 600;
      font-size: 24px;
      letter-spacing: 0.05em;
      margin-right: 12px;

      @media(max-width: 1440px) {
        font-size: 22px;
      }

      @media(max-width: 1280px) {
        font-size: 20px;
      }

      @media(max-width: 1024px) {
        font-size: 18px;
      }
      @media(max-width: 768px) {
        font-size: 17px;
      }
    }

    &-discount {
      font-family: 'FuturaDemiC';
      font-weight: 600;
      font-size: 24px;
      text-decoration: line-through;
      color: #728EAE;
      letter-spacing: 0.05em;

      @media(max-width: 1024px) {
        font-size: 18px;
      }
      @media(max-width: 768px) {
        font-size: 17px;
      }
    }

    &-cart {
      position: relative;
      width: 56px;
      height: 56px;
      border-radius: 50%;
      background: #F2F2F2;
      cursor: pointer;
      transition: 0.1s ease-out;

      @media(max-width: 1440px) {
        width: 48px;
        height: 48px;
      }

      @media(max-width: 1024px) {
        width: 46px;
        height: 46px;
      }

      @media(max-width: 768px) {
        width: 36px;
        height: 36px;
      }

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);


        @media(max-width: 768px) {
          max-width: 50%;
        }
      }

      &:hover {
        background: #a8bed6;
      }
    }
  }

  &-control {
    margin-bottom: 64px;

    @media(max-width: 1440px) {
      margin-bottom: 50px;
    }

    @media(max-width: 1280px) {
      margin-bottom: 40px;
    }

    @media(max-width: 1024px) {
      margin-bottom: 15px;
    }

    &.flex {
      justify-content: space-between;
    }
  }

  &-btn {
    &-previous {
      margin-right: 24px;
    }

    @media(max-width: 1024px) {
      display: none;
    }
  }
}

.swiper-pagination.new-pagination {
  display: none;

  @media(max-width: 1024px) {
    bottom: 0;
    display: block;
  }
}

.swiper-pagination.rec-pagination {
  display: none;

  @media(max-width: 1024px) {
    bottom: 0;
    display: block;
  }
}